} */

.sidebar ul li {
  border-bottom: 0px solid #ffffff;
  padding: 0px;
}

.sidebar ul li a {
  display: block;
  font-size: 14px;
  color: #ffffff;
  padding: 15px 25px;
  font-weight: 700;
  text-decoration: none;
}

.sidebar ul li a.active {
  background-color: #007bff !important;
  color: #ffffff !important;
}

.skoverlay {
  height: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 auto 15px;
  max-width: 100%;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  display: -webkit-box;
}

.skoverlay p {
  color: #666e86;
  font-size: 14px;
  line-height: 22px;
}

.search_bar {
  position: relative;
}

.search_bar input {
  font-size: 14px;
  font-weight: 500;
  border: 0;
  padding: 10px 24px;
  min-width: 100%;
  box-shadow: 0px 1px 5px #e2e2e2;
  outline: none;
}

.search_bar svg {
  position: absolute;
  right: 10px;
  top: 11px;
  font-size: 19px;
  color: #757575;
}
.MuiButton-containedSizeSmall {
  padding: 8px 8px;
  width: auto !important;
  min-width: auto !important;
  text-align: center;
}
.MuiButton-startIcon.MuiButton-iconSizeSmall {
  margin: 0px !important;
  margin-left: -2px;
}

.ellipsis_class {
  height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 auto 15px;
  max-width: 100%;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  text-align: left;
}

.required {
  content: " *";
  color: red;
}

